import React from 'react';
import imgArrowW from '../images/arrow_w.svg';
import imgMarker from '../images/marker.svg';
import imgPrDiv from '../images/pr_div.svg';

export default class PropertyMainInfo extends React.Component {
  render () {
    const { componentType, propertyDetails } = this.props;
    return (
      <>
        {componentType === 'desktop' &&
          <div className='idx_info desktop'>
            <div className='address animated_fastest' style={{ marginBottom: '20px' }}>{propertyDetails.address1}
              <div className='add_2'><div className='marker'><img src={imgMarker} width='100%' alt='Address Marker' /></div> {propertyDetails.addressCity}
                <div className='pr_div'><img src={imgPrDiv} width='100%' alt='div' /></div> {propertyDetails.listPriceFormatted}
              </div>
              <div className='divider' />
            </div>
            <div className='row animated_fastest'>
              <div className='numb_block'>{propertyDetails.bedRoomsTotal}
                <div className='n_title'>Beds</div>
              </div>
              <div className='numb_block'>{propertyDetails.bathRoomsTotal}
                <div className='n_title'>Baths</div>
              </div>
              <div className='numb_block2'>{propertyDetails.livingAreaSqFeetFormatted} Sq. Ft.
                <div className='n_title'>{propertyDetails.pricePerSqFeetFormatted} / Sq. Ft.</div>
              </div>
            </div>
            <div className='row animated_fastest' style={{ marginBottom: '30px', maxWidth: '250px' }}>
              {PropertyInfoRows(propertyDetails)}
            </div>
            {
              false &&
                <>
                  <div className='save_btn2 animated_fastest'>SAVE</div>
                  <div className='next_btn2 animated_fastest'>SHARE<div className='lm_arrow'><img src={imgArrowW} width='100%' alt='Arrow W' /></div></div>
                </>
            }
          </div>}

        {componentType === 'mobile' &&
          <div className='idx_info mobile'>
            <div className='address animated_fastest' style={{ marginBottom: '20px' }}>
              <span>{propertyDetails.address1}</span>
              <div className='add_2'><div className='marker'><img src={imgMarker} width='100%' alt='Address Marker' /></div>
                <div>{propertyDetails.addressCity}</div>
                <div className='pr_div'><img src={imgPrDiv} width='100%' alt='div' /></div> {propertyDetails.listPriceFormatted}
              </div>
              <div className='divider' />
            </div>
            <div className='row'>
              <div className='numb_block'>{propertyDetails.bedRoomsTotal}<div className='n_title'>Beds</div></div>
              <div className='numb_block'>{propertyDetails.bathRoomsTotal}<div className='n_title'>Baths</div></div>
              <div className='numb_block2'>{propertyDetails.livingAreaSqFeetFormatted} Sq. Ft.<div className='n_title'>{propertyDetails.pricePerSqFeetFormatted} / Sq. Ft.</div></div>
            </div>
            <div className='row'>
              {PropertyInfoRows(propertyDetails)}
            </div>
            {
              false &&
                <>
                  <div className='save_btn2 animated_fastest'>SAVE</div>
                  <div className='next_btn2 animated_fastest'>SHARE<div className='lm_arrow'><img src={imgArrowW} width='100%' alt='arrow' /></div></div>
                </>
            }
          </div>}
      </>
    );
  }
}

const PropertyInfoRows = (propertyDetails) => {
  return (
    <>
      <div className='r_info'>Status</div>
      <div className='r_info'>{propertyDetails.MLSStatus}</div>
      <div className='r_info'>Year Built</div>
      <div className='r_info'>{propertyDetails.yearBuilt}</div>
      <div className='r_info'>Days on Market</div>
      <div className='r_info'>{propertyDetails.daysOnMarket}</div>
      <div className='r_info'>Style</div>
      <div className='r_info'>{propertyDetails.architecturalStyle}</div>
      <div className='r_info'>Estimated Taxes</div>
      <div className='r_info'>{propertyDetails.estimatedTaxesFormatted}</div>
      <div className='r_info'>HOA Fees</div>
      <div className='r_info'>{propertyDetails.associationFeeFormatted} {propertyDetails.associationFeeFrequency}</div>
      <div className='r_info'>Area</div>
      <div className='r_info'>{propertyDetails.MLSAreaMajor}</div>
    </>
  );
};
