import React from 'react';
import HeaderProperty from '../components/Shared/Header_property';
import Footer from '../components/Shared/Footer';
import PropertyIdx from './PropertyIdx';
import PropertyDetails from './PropertyDetails';
import qs from 'query-string';
import './SelectNormal.css';

export default class IdxDashboard extends React.Component {
  componentDidMount () {

  }

  render () {
    var propertyId = qs.parse(this.props.location.search, { ignoreQueryPrefix: true }).propertyid;

    return (
      <>
        <div className='se-pre-con'><div className='loader' /></div>
        <div style={{ minHeight: '500px' }}>
          {
            propertyId === undefined &&
              <PropertyIdx />
          }
          {
            propertyId !== undefined &&
              <>
                <HeaderProperty />
                <PropertyDetails propertyId={propertyId} />
              </>
          }
        </div>
        <Footer />
      </>
    );
  }
}
