import React from 'react';
import axios from 'axios';
import * as propertyUtil from './PropertyUtil';
// import $ from 'jquery';
import PropertyPhotos from '../components/PropertyPhotos';
import Neighborhood from '../components/Neighborhood';
import SchoolsDaycare from '../components/SchoolsDaycare';
import Amenities from '../components/Amenities';
import PropertyMainInfo from '../components/PropertyMainInfo';
import PropertyRemarks from '../components/PropertyRemarks';
import LocationsOpenHouseTimes from '../components/LocationsOpenHouseTimes';
import OfferForm from '../components/OfferForm';

var $ = window.jQuery;

export default class PropertyDetails extends React.Component {
  constructor (props) {
    super(props);

    this.state = {
      propertyDetails: undefined,
      notFound: false,
      loadingError: false,
      cycle2Loaded: false
    };
  }

  async componentDidMount () {
    if (this.props.propertyId !== undefined) {
      const url = `${process.env.REACT_APP_RIVERDALE_PUBLIC_API}/properties/${this.props.propertyId}`;
      const requestConfig = {
        headers: {
          'Content-Type': 'application/json; charset=utf-8',
          'X-Api-Key': process.env.REACT_APP_RIVERDALE_PUBLIC_API_KEY
        }
      };
      try {
        const response = await axios.get(url, requestConfig);
        const propertyDetails = response.data;
        this.setState({ propertyDetails, notFound: false, loadingError: false });

        const google = window.google;

        const contentString = '<div id="content">' +
              '<div id="siteNotice">' +
              '</div>' +
              `<div id="firstHeading" className="firstHeading">${propertyDetails.address1}<br />${propertyDetails.address2}<div className="priceiw">${propertyDetails.listPriceFormatted}</div></div>` +
              '<div id="bodyContent">' + propertyDetails.structure + '</div>' +
              '</div>';

        const infowindow = new google.maps.InfoWindow({
          content: contentString
        });

        var centerLatitude = propertyDetails.latitude;
        var centerLongitude = propertyDetails.longitude;

        var mapOptions = {
        // How zoomed in you want the map to start at (always required)
          zoom: 15,
          gestureHandling: 'cooperative',
          disableDefaultUI: true,
          // The latitude and longitude to center the map (always required)
          center: new google.maps.LatLng(centerLatitude, centerLongitude),
          styles: propertyUtil.mapStyles
        };

        // Get the HTML DOM element that will contain your map
        // We are using a div with id="map" seen below in the <body>
        var mapElement = document.getElementById('maps');

        // Create the Google Map using our element and options defined above
        var map = new google.maps.Map(mapElement, mapOptions);
        // marker position
        var factory = new google.maps.LatLng(centerLatitude, centerLongitude);

        // InfoWindow content
        var icon = {
          url: `${Window.public_url}/img/marker2.svg`,
          scaledSize: new google.maps.Size(20, 32),
          origin: new google.maps.Point(0, 0), // origin
          anchor: new google.maps.Point(0, 0) // anchor
        };

        var marker = new google.maps.Marker({
          position: factory,
          map: map,
          icon: icon
        });

        // marker options
        map.setCenter(factory);

        // This event expects a click on a marker
        // When this event is fired the Info Window is opened.
        google.maps.event.addListener(marker, 'click', function () {
          infowindow.open(map, marker);
        });

        // Event that closes the Info Window with a click on the map
        google.maps.event.addListener(map, 'click', function () {
          infowindow.close();
        });

        // *
        // START INFOWINDOW CUSTOMIZE.
        // The google.maps.event.addListener() event expects
        // the creation of the infowindow HTML structure 'domready'
        // and before the opening of the infowindow, defined styles are applied.
        // *
        google.maps.event.addListener(infowindow, 'domready', function () {
          // Reference to the DIV that wraps the bottom of infowindow
          var iwOuter = $('.gm-style-iw');

          /* Since this div is in a position prior to .gm-div style-iw.
          * We use jQuery and create a iwBackground variable,
          * and took advantage of the existing reference .gm-style-iw for the previous div with .prev().
          */
          var iwBackground = iwOuter.prev();

          // Removes background shadow DIV
          iwBackground.children(':nth-child(2)').css({ display: 'none' });

          // Removes white background DIV
          iwBackground.children(':nth-child(4)').css({ display: 'none' });

          // Moves the infowindow 115px to the right.
          // iwOuter.parent().parent().css({ left: '115px' });

          // Moves the shadow of the arrow 76px to the left margin.
          iwBackground.children(':nth-child(1)').attr('style', function (i, s) { return s + 'left: 140px !important;'; });

          // Moves the arrow 76px to the left margin.
          iwBackground.children(':nth-child(3)').attr('style', function (i, s) { return s + 'left: 140px !important;'; });

          // Changes the desired tail shadow color.
          iwBackground.children(':nth-child(3)').find('div').children().css({ opacity: '0', 'box-shadow': 'rgba(0, 0, 0, 0.0) 0px 1px 6px', 'z-index': '1' });

          // Reference to the div that groups the close button elements.
          var iwCloseBtn = iwOuter.next();

          // Apply the desired effect to the close button
          iwCloseBtn.css({ opacity: '0', right: '48px', background: '#ac441e', top: '20px', color: 'white', border: 'none', 'border-radius': '0', 'box-shadow': '0' });

          // If the content of infowindow not exceed the set maximum height, then the gradient is removed.
          if ($('.iw-content').height() < 140) {
            $('.iw-bottom-gradient').css({ display: 'none' });
          }

          // The API automatically applies 0.7 opacity to the button after the mouseout event. This function reverses this event to the desired value.
          iwCloseBtn.mouseout(function () {
            $(this).css({ opacity: '1' });
          });
        });
      } catch (error) {
        console.log(error);
        var notFound = false;

        if (error.response !== undefined) {
          if (error.response.status === 404) {
            notFound = true;
          }
        }
        this.setState({ propertyDetails: undefined, notFound, loadingError: true });
      }

      propertyUtil.loadScript(`${Window.public_url}/dist/js/cycle2.js`, 'idx-cycle2', () => { this.setState({ cycle2Loaded: true }); });
      propertyUtil.loadScript(`${Window.public_url}/dist/js/caption2.js`, 'idx-caption2', () => { this.setState({ caption2Loaded: true }); });
      propertyUtil.loadScript(`${Window.public_url}/dist/js/swipe.js`, 'idx-swipe', () => { this.setState({ swipeLoaded: true }); });
      propertyUtil.loadScript(`${Window.public_url}/dist/js/main.js`, 'idx-main', () => { this.setState({ mainLoaded: true }); });
    }
  }

  render () {
    var propertyDetails;
    if (this.state.propertyDetails !== undefined) {
      propertyDetails = this.state.propertyDetails;
    }

    return (
      <>
        {
          (this.state.loadingError === true && this.state.notFound === true) &&
            <div className='alert_text'>Property does not exist!</div>
        }

        {
          (this.state.loadingError === true && this.state.notFound === false) &&
            <div className='alert_text'>Property does not exist!</div>
        }

        {
          (this.props.propertyId.trim() !== '' && propertyDetails !== undefined) &&
            <div className='idx_content'>
              <div className='id_top'>
                <div className='idx_slideshow'>
                  <PropertyPhotos
                    listPrice={propertyDetails.listPriceFormatted}
                    photos={propertyDetails.photos}
                  />
                  <PropertyMainInfo componentType='mobile' propertyDetails={propertyDetails} />

                  <div className='about_content'>
                    <PropertyRemarks propertyDetails={propertyDetails} />
                    <Amenities propertyDetails={propertyDetails} />
                    {false && <Neighborhood />}
                    {false && <SchoolsDaycare />}
                  </div>

                  <LocationsOpenHouseTimes propertyDetails={propertyDetails} />

                  <OfferForm propertyDetails={propertyDetails} />
                </div>

                <PropertyMainInfo componentType='desktop' propertyDetails={propertyDetails} />
              </div>
            </div>
        }
      </>
    );
  }
}
