import React from 'react';
import axios from 'axios';
import imgList from '../images/list_img.jpg';
import closeFoot from '../images/x_over.svg';
import imgArrowW from '../images/arrow_w.svg';
import imgNewArrow from '../images/newarrow.svg';
import imgAnotherArrowOvers from '../images/another_arrow_overs.svg';
import imgAnotherArrow from '../images/another_arrow.svg';
import uuidv4 from 'uuid/v4';
import * as propertyUtil from './PropertyUtil';
import Select from 'react-select';
import HeaderListing from '../components/Shared/Header_listing';
import HeaderMap from '../components/Shared/Header_map';
var $ = window.jQuery;

export default class PropertyIdx extends React.Component {
  constructor () {
    super();
    this.handleSearchConditionChange = this.handleSearchConditionChange.bind(this);
    this.handleMainSearch = this.handleMainSearch.bind(this);
    this.handleMainSubmit = this.handleMainSubmit.bind(this);
    this.handleMapSearch = this.handleMapSearch.bind(this);
    this.handleMapSubmit = this.handleMapSubmit.bind(this);
    this.handleListSearch = this.handleListSearch.bind(this);
    this.handleListSubmit = this.handleListSubmit.bind(this);
    this.handleFilterChange = this.handleFilterChange.bind(this);
    this.handleToggleFilter = this.handleToggleFilter.bind(this);

    this.handleMapSearchChange = this.handleMapSearchChange.bind(this);
    this.handleListSearchChange = this.handleListSearchChange.bind(this);

    this.handleShowList = this.handleShowList.bind(this);
    this.handleShowMap = this.handleShowMap.bind(this);

    this.mainSearchConditionRef = React.createRef();
    this.mapSearchConditionRef = React.createRef();
    this.listSearchConditionRef = React.createRef();

    this.state = {
      SearchCondition: '',
      SearchConditionDisplay: '',
      PropertiesList: undefined,
      SearchTransId: '',
      ShowMaps: false,
      FilterBeds: undefined,
      FilterBaths: undefined,
      FilterMinListPrice: undefined,
      FilterMaxListPrice: undefined,
      // FilterMinSquare: undefined,
      // FilterMaxSquare: undefined,
      FilterOpened: false
    };
  }

  handleSearchConditionChange (event) {
    this.setState({ SearchCondition: event.target.value });
  }

  handleFilterChange (selectedItem, target) {
    if (target.action === 'select-option') {
      this.setState({ [target.name]: selectedItem });
    } else if (target.action === 'clear') {
      this.setState({ [target.name]: undefined });
    }
  }

  handleToggleFilter () {
    if (this.state.FilterOpened === true) {
      this.setState({ FilterOpened: false });
    } else {
      this.setState({ FilterOpened: true });
    }
  }

  shouldComponentUpdate (nextProps, nextState) {
    if (this.state.SearchTransId !== nextState.SearchTransId ||
      this.state.SearchCondition !== nextState.SearchCondition ||
      // this.state.FilterBeds !== nextState.FilterBeds ||
      // this.state.FilterBaths !== nextState.FilterBaths ||
      // this.state.FilterMinListPrice !== nextState.FilterMinListPrice ||
      // this.state.FilterMaxListPrice !== nextState.FilterMaxListPrice ||
      // this.state.FilterMinSquare !== nextState.FilterMinSquare ||
      // this.state.FilterMaxSquare !== nextState.FilterMaxSquare ||
      this.state.FilterOpened !== nextState.FilterOpened
    ) {
      return true;
    } else {
      return false;
    }
  }

  componentDidMount () {
    propertyUtil.loadScript(`${Window.public_url}/dist/js/main.js`, 'idx-main', () => { this.setState({ mainLoaded: true }); });
    if (this.mainSearchConditionRef.current !== undefined) {
      this.mainSearchConditionRef.current.focus();
    }
  }

  componentDidUpdate (prevProps, prevState) {
    if (this.state.PropertiesList !== undefined && this.state.SearchTransId !== prevState.SearchTransId) {
      const properties = this.state.PropertiesList;
      const google = window.google;

      var centerLatitude = 33.989079;
      var centerLongitude = -118.443024;

      var features = [];
      properties.forEach(property => {
        centerLatitude = property.latitude;
        centerLongitude = property.longitude;

        // console.log(property.listPrice, listPriceFormatted);
        features.push({
          ...property,
          position: new google.maps.LatLng(property.latitude, property.longitude),
          type: 'info',
          text: property.listPriceAbbr,
          price: property.listPriceFormatted,
          color: '#FFF'
        });
      });

      var mapOptions = {
        // How zoomed in you want the map to start at (always required)
        zoom: 15,
        gestureHandling: 'cooperative',
        disableDefaultUI: true,
        // The latitude and longitude to center the map (always required)
        center: new google.maps.LatLng(centerLatitude, centerLongitude),
        styles: propertyUtil.mapStyles
      };

      // Get the HTML DOM element that will contain your map
      // We are using a div with id="map" seen below in the <body>
      var mapElement = document.getElementById('fullmap');

      // Create the Google Map using our element and options defined above
      var map = new google.maps.Map(mapElement, mapOptions);

      var icons = {
        nile: {
          icon: {
            url: `${Window.public_url}/img/nile_listing_box.svg`,
            scaledSize: new google.maps.Size(83, 25),
            origin: { x: -6, y: 0 }

          }
        },
        library: {
          icon: `${Window.public_url}/img/marker2.svg`
        },
        parking: {
          icon: `${Window.public_url}/img/marker2.svg`
        },
        info: {
          color: '#000',
          icon: {
            url: `${Window.public_url}/img/nile_listing_box.svg`,
            scaledSize: new google.maps.Size(83, 25),
            origin: { x: -6, y: 0 }
          }
        }
      };

      // Create markers.
      for (var i = 0; i < features.length; i++) {
        const featureText = features[i].text;
        const marker = new google.maps.Marker({
          position: features[i].position,
          icon: icons[features[i].type].icon,
          map: map,
          label: {
            text: `${featureText}`,
            color: features[i].color,
            fontSize: '12px',
            fontFamily: 'Gilroy-SemiBold'
          },
          visible: true
        });

        const contentString = '<div id="content">' +
              '<div id="siteNotice">' +
              '</div>' +
              `<div id="firstHeading" class="firstHeading">${features[i].address1}<br />${features[i].address2}<div class="priceiw">${features[i].price}</div></div>` +
              '<div id="bodyContent">' + features[i].structure + '</div>' +
              `<div class='viewDetailsContainer'><a class='viewDetailsLink' target='_blank' rel='noopener noreferrer' href='?propertyid=${features[i].propertyId}'>VIEW DETAILS</a></div>` +
              '</div>';

        const infowindow = new google.maps.InfoWindow({
          content: contentString
        });

        marker.addListener('click', function () {
          infowindow.open(map, marker);
        });
      }

      propertyUtil.initPropertyIdxUIActions($);

      if (this.mapSearchConditionRef.current !== undefined) {
        if (this.mapSearchConditionRef.current.value === '') {
          this.mapSearchConditionRef.current.value = this.state.SearchCondition;
        }
      }

      if (this.listSearchConditionRef.current !== undefined) {
        if (this.listSearchConditionRef.current.value === '') {
          this.listSearchConditionRef.current.value = this.state.SearchCondition;
        }
      }
    }
  }

  handleShowList () {
    if (this.listSearchConditionRef.current !== undefined) {
      this.listSearchConditionRef.current.value = this.state.SearchConditionDisplay;
    }
  }

  handleShowMap () {
    if (this.mapSearchConditionRef.current !== undefined) {
      this.mapSearchConditionRef.current.value = this.state.SearchConditionDisplay;
    }
  }

  handleMapSearchChange () {
    this.setState({ SearchConditionDisplay: this.mapSearchConditionRef.current.value });
  }

  handleListSearchChange () {
    this.setState({ SearchConditionDisplay: this.listSearchConditionRef.current.value });
  }

  async handleMapSearch () {
    this.searchProperties(this.mapSearchConditionRef.current.value);
  }

  async handleMapSubmit (event) {
    event.preventDefault();
    this.searchProperties(this.mapSearchConditionRef.current.value);
  }

  async handleListSearch () {
    this.searchProperties(this.listSearchConditionRef.current.value);
  }

  async handleListSubmit (event) {
    event.preventDefault();
    this.searchProperties(this.listSearchConditionRef.current.value);
  }

  async handleMainSearch () {
    this.searchProperties(this.state.SearchCondition);
  }

  async handleMainSubmit (event) {
    event.preventDefault();
    this.searchProperties(this.state.SearchCondition);
  }

  async searchProperties (searchCondition) {
    try {
      $('body').css('cursor', 'progress');
      var url = `${process.env.REACT_APP_RIVERDALE_PUBLIC_API}/properties`;

      if (searchCondition !== undefined && searchCondition !== '') {
        url += `?search=${searchCondition}`;
      } else {
        url += '?search=';
      }

      if (this.state.FilterBeds !== undefined) {
        url += `&bedrooms=${this.state.FilterBeds.value}`;
      }

      if (this.state.FilterBaths !== undefined) {
        url += `&bathrooms=${this.state.FilterBaths.value}`;
      }

      if (this.state.FilterMinListPrice !== undefined) {
        url += `&price1=${this.state.FilterMinListPrice.value}`;
      }

      if (this.state.FilterMaxListPrice !== undefined) {
        url += `&price2=${this.state.FilterMaxListPrice.value}`;
      }

      // if (this.state.FilterMinSquare !== undefined || this.state.FilterMaxSquare !== undefined) {
      //   var minSquare = 0;
      //   var maxSquare = 9999999999;
      //   if (this.state.FilterMinSquare !== undefined) {
      //     minSquare = this.state.FilterMinSquare.value;
      //   }

      //   if (this.state.FilterMaxSquare !== undefined) {
      //     maxSquare = this.state.FilterMaxSquare.value;
      //   }

      //   if (minSquare > maxSquare) {
      //     var tmpSquare = minSquare;
      //     minSquare = maxSquare;
      //     maxSquare = tmpSquare;
      //   }
      // // url += `&min_list_price=${minSquare}`;
      // // url += `&max_list_price=${maxSquare}`;
      // }

      const requestConfig = {
        headers: {
          'Content-Type': 'application/json',
          'X-Api-Key': process.env.REACT_APP_RIVERDALE_PUBLIC_API_KEY
        }
      };
      const response = await axios.get(url, requestConfig);
      this.setState({ PropertiesList: response.data, ShowMaps: true, SearchTransId: uuidv4() });
    } catch (error) {
      console.log(error);
    }
    $('body').css('cursor', 'default');
  }

  render () {
    const properties = this.state.PropertiesList;

    return (
      <>
        <span id='map-page' style={{ display: 'none' }} />
        {
          this.state.PropertiesList === undefined &&
            <>
              <HeaderListing inverseBackground />
              <span id='listing-page' style={{ display: 'none' }} />
              <div className='listing_img'>
                <div className='under_copy animated_regular downss'>
                  <div className='under_closer animated_regular'><img src={closeFoot} width='100%' alt='close' /></div>
                  <div className='under_inner'> NILE search is in beta. Our team is busy rolling out new features and properties in order to give you the best possible search experience. In the interim, if you canât find the property you were looking for, please feel free to use one of our search partners,  <a href='http://www.mls.com/' taregt='_blank' rel='noopener noreferrer'>MLS</a> or <a href='https://www.zillow.com/' target='_blank' rel='noopener noreferrer'>Zillow</a>. </div>
                </div>
                <div className='listing_photo'>
                  <div className='list_overlayer' />
                  <div className='limg'>
                    <div style={{ backgroundImage: `url(${imgList})`, height: '100vh', width: '100%', backgroundSize: 'cover' }} />
                  </div>
                  <form onSubmit={this.handleMainSubmit}>
                    <div className='list_header'><span>Your home</span>
                      <div className='l_head2'>awaits</div>
                      <div className='list_input'>
                        <input
                          ref={this.mainSearchConditionRef}
                          className='form2 anotherform'
                          placeholder='Enter a City, Address, MLS Number, or Zip Code'
                          value={this.state.SearchCondition}
                          onChange={this.handleSearchConditionChange}
                        />
                        <div className='send_btn animated_fastest' style={{ cursor: 'pointer' }} onClick={this.handleMainSearch}>
                          <span>SEARCH</span>
                          <div className='lm_arrow'>
                            <img src={imgArrowW} style={{ marginLeft: '0' }} width='75%' alt='Arrow W' />
                          </div>
                        </div>
                        <button type='submit' style={{ display: 'none' }}>SEARCH</button>
                      </div>

                      <div className='form_inputs' style={{ lineHeight: '18px', display: 'none' }}>
                        <div className='inputer_form halfs' style={{ marginBottom: '0px' }}>
                          <div className='custom-select alternateee' />
                        </div>
                        <div onClick={this.handleMainSearch} className='send_btn animated_fastest mobile' style={{ marginTop: '30px', paddingTop: '9px', paddingBottom: '4px' }}>
                          <span>SEARCH</span>
                          <div className='lm_arrow'><img src={imgArrowW} style={{ marginLeft: '0' }} width='75%' alt='Arrow W' />
                          </div>
                        </div>
                      </div>
                      <div className='formsholder'>
                        <div className='form_inputs newclass' style={{ lineHeight: '18px' }}>
                          <div className='folt_head'>Features:</div>
                          <div className='inputer_form thirds' style={{ marginBottom: '0px' }}>
                            <Select
                              name='FilterBeds'
                              options={BedsOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='Beds'
                              classNamePrefix='react-select'
                            />
                          </div>
                          <div className='inputer_form thirds' style={{ marginBottom: '0px' }}>
                            <Select
                              name='FilterBaths'
                              options={BathsOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='Baths'
                              classNamePrefix='react-select'
                            />
                          </div>
                        </div>
                        <div className='form_inputs newclass' style={{ lineHeight: '18px' }}>
                          <div className='folt_head'>Pricing:</div>
                          <div className='inputer_form thirds' style={{ marginBottom: '0px' }}>
                            <Select
                              name='FilterMinListPrice'
                              options={MinListPriceOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='No Min'
                              classNamePrefix='react-select'
                            />
                          </div>
                          <div className='inputer_form thirds' style={{ marginBottom: '0px' }}>
                            <Select
                              name='FilterMaxListPrice'
                              options={MaxListPriceOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='No Max'
                              classNamePrefix='react-select'
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
              <div className='other_content'>
                <div style={{ height: '100vh' }}>&nbsp;</div>
              </div>
            </>
        }

        {
          this.state.PropertiesList !== undefined &&
            <>
              <HeaderMap />
              <div className='switch mobile animated_fastest'>
                <div className='switch_btns'>
                  <div id='list1' className='f_btn f_actives'>Listings</div>
                  <div id='list2' className='f_btn'>Map</div>
                </div>
              </div>

              <div className='map_holder'>
                <div className='map_header desktop'>Current Listings</div>
                <div id='h_1' className='map_btn animated_fastest desktop hiddens' onClick={this.handleShowList}>Hide Map</div>

                <form onSubmit={this.handleMapSubmit}>
                  <div className='searcher animated_fastest hiddens'>
                    {/* map search */}
                    <input
                      ref={this.mapSearchConditionRef}
                      onChange={this.handleMapSearchChange}
                      className='form2 anotherform2'
                      placeholder='Enter a City, Address, MLS Number, or Zip Code'
                    />
                    <div className='search_btn animated_fastest' onClick={this.handleMapSearch}>SEARCH </div>
                    <button type='submit' style={{ display: 'none' }}>SEARCH</button>
                  </div>
                </form>
                <div className='right_side animated_fastest hiddens'>
                  <div className='closer_arrow animated_fastest desktop hiddens'>
                    <div className='c_arrow'>
                      <img src={imgAnotherArrowOvers} className='overs animated_fastest' width='100%' alt='Arrow Overs' />
                      <img className='animated_fastest' src={imgAnotherArrow} width='100%' alt='Arrow' />
                    </div>
                  </div>
                  <div className='filter'>
                    <div className='filter_btn animated_fastest' onClick={this.handleToggleFilter}><span>Filter by</span>
                      <div className='filter_arrow animated_fastest'>
                        <img src={imgNewArrow} width='100%' alt='New Arrow' />
                      </div>
                    </div>
                  </div>

                  {this.state.FilterOpened === true &&
                    <div id='filt1' className='filtering_panel animated_regular'>
                      <div className='filter_top'>
                        <div className='filterssing'>
                          <div className='filts_he'>Price</div>
                          <div className='custom-select filterz'>
                            <Select
                              name='FilterMinListPrice'
                              options={MinListPriceOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='No Min'
                              classNamePrefix='react-select'
                            />
                          </div>
                          <div className='filts_he' style={{ textAlign: 'center' }}>to</div>
                          <div className='custom-select filterz'>
                            <Select
                              name='FilterMaxListPrice'
                              options={MaxListPriceOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='No Max'
                              classNamePrefix='react-select'
                            />
                          </div>

                        </div>
                        <div className='filterssing' style={{ marginTop: '15px' }}>
                          <div className='filts_he'>Beds</div>
                          <div className='custom-select filterz'>
                            <Select
                              name='FilterBeds'
                              options={BedsOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='Beds'
                              classNamePrefix='react-select'
                            />
                          </div>
                          <div className='filts_he'>Baths</div>
                          <div className='custom-select filterz'>
                            <Select
                              name='FilterBaths'
                              options={BathsOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='Baths'
                              classNamePrefix='react-select'
                            />
                          </div>
                        </div>
                      </div>
                    </div>}

                  <div className='right_scorlls'>
                    {
                      properties.map(property =>
                        <a key={uuidv4()} href={`?propertyid=${property.propertyId}`} target='_blank' rel='noopener noreferrer' className='target_block'>
                          <div className='t_img'>
                            <div
                              className='property_image' style={{
                                backgroundImage: `url(${property.firstPhotoUrl})`
                              }}
                            />
                          </div>
                          <div className='t_infoss'>
                            {property.address1}<br />
                            {property.address2}<br />
                            {property.structure}<br />
                            {property.listPriceFormatted}
                          </div>
                        </a>
                      )
                    }

                    <div className='vlocker' />
                  </div>
                </div>

                <div className='right_side_overs animated_fastest desktop'>
                  <div className='fixed_filter_menu'>
                    <div className='filter_inner'>
                      <div>
                        <div id='h_2' className='map_btn2 animated_fastest' onClick={this.handleShowMap}>Show Map</div>
                      </div>
                      <div className='filtering_panel'>
                        <div className='filtering_list_filters'>
                          <div className='list_filts_he'>Price</div>
                          <div className='custom-select list_filterz'>
                            <Select
                              name='FilterMinListPrice'
                              options={MinListPriceOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='No Min'
                              classNamePrefix='react-select'
                            />
                          </div>
                          <div className='list_filts_he' style={{ textAlign: 'center' }}>to</div>
                          <div className='custom-select list_filterz'>
                            <Select
                              name='FilterMaxListPrice'
                              options={MaxListPriceOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='No Max'
                              classNamePrefix='react-select'
                            />
                          </div>
                          <div className='list_filts_he'>Beds</div>
                          <div className='custom-select list_filterz'>
                            <Select
                              name='FilterBeds'
                              options={BedsOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='Beds'
                              classNamePrefix='react-select'
                            />
                          </div>
                          <div className='list_filts_he'>Baths</div>
                          <div className='custom-select list_filterz'>
                            <Select
                              name='FilterBaths'
                              options={BathsOptions}
                              onChange={this.handleFilterChange}
                              isClearable
                              isSearchable={false}
                              placeholder='Baths'
                              classNamePrefix='react-select'
                            />
                          </div>
                        </div>
                      </div>
                      {/* list search */}
                      <div>
                        <form onSubmit={this.handleListSubmit}>
                          <input
                            ref={this.listSearchConditionRef}
                            onChange={this.handleListSearchChange}
                            className='form2'
                            style={{ marginRight: '2%', width: '75%' }}
                            placeholder='Enter a City, Address, MLS Number, or Zip Code'
                          />
                          <div style={{ width: '22%', marginTop: '-2px' }} className='search_btn animated_fastest' onClick={this.handleListSearch}>SEARCH </div>
                          <button type='submit' style={{ display: 'none' }}>SEARCH</button>
                        </form>
                      </div>
                    </div>
                  </div>
                  <div className='movedowsn'>
                    {
                      properties.map(property =>
                        <a key={uuidv4()} className='target_block_overs property_block' target='_blank' rel='noopener noreferrer' href={`?propertyid=${property.propertyId}`}>
                          <div className='t_img'>
                            <div
                              className='property_image' style={{
                                backgroundImage: `url(${property.firstPhotoUrl})`
                              }}
                            />
                          </div>
                          {property.address1}<br />
                          {property.address2}<br />
                          {property.structure}<br />
                          {property.listPriceFormatted}
                        </a>
                      )
                    }
                  </div>
                </div>
                <div id='fullmap' className='desktop' style={{ position: 'relative', overflow: 'hidden', display: 'none' }} />
              </div>
            </>
        }
      </>
    );
  }
}

const BedsOptions = [
  { label: '1 Bed', value: '1' },
  { label: '2 Beds', value: '2' },
  { label: '3 Beds', value: '3' },
  { label: '4 Beds', value: '4' }
];

