import React from 'react';
import imgArrowNover from '../images/arrow_nover.svg';
import imgArrowN from '../images/arrow_n.svg';
import uuidv4 from 'uuid/v4';

export default class PropertyPhotos extends React.Component {
  shouldComponentUpdate (nextProps, nextState) {
    if (this.props.photos !== nextProps.photos) {
      return true;
    } else {
      return false;
    }
  }

  render () {
    const { listPrice, photos } = this.props;

    return (
      <div className='slideshowsidx'>
        <div className='idx_price'>{listPrice}</div>
        <div
          className='cycle-slideshow slideshow-listings'
          data-cycle-fx='scrollHorz'
          data-cycle-timeout='4500'
          data-cycle-prev='.left_arrow'
          data-cycle-next='.right_arrow'
          data-cycle-manual-trump='false'
          data-cycle-speed='1000'
          data-cycle-swipe='true'
          data-cycle-paused='false'
          data-cycle-pause-on-hover='true'
          data-cycle-pager='#adv-custom-pager'
          data-cycle-pager-template="<a class='thumbs' href='#'><img src='{{firstChild.src}}' width='100%' /></a>"
          data-cycle-slides='> .slieimg'
        >
          <div className='arrows'>
            <div className='slide_arrow left_arrow'>
              <img src={imgArrowNover} className='overs animated_fastest' width='100%' alt='Go prev' />
              <img src={imgArrowN} width='100%' alt='Arrow N' />
            </div>
            <div className='slide_arrow right_arrow'>
              <img src={imgArrowNover} className='overs animated_fastest' width='100%' alt='Go next' />
              <img src={imgArrowN} width='100%' alt='Arrow N' />
            </div>
          </div>
          {
            photos !== undefined &&
            photos.map(photo =>
              <div key={uuidv4()} className='slieimg'>
                <img src={photo} alt='property' style={{ display: 'none' }} />
                <div style={{ backgroundImage: `url(${photo})`, height: '680px', backgroundPosition: 'center', backgroundRepeat: 'no-repeat', backgroundSize: '891px 594px' }} />
              </div>
            )
          }
        </div><div id='adv-custom-pager' />
      </div>
    );
  }
}
