import React from 'react';
import uuidv4 from 'uuid/v4';

export default class Amenities extends React.Component {
  render () {
    const { propertyDetails } = this.props;

    return (
      <div className='about_block'>
        <div className='about_header'>Amenities</div>
        <div className='about_infos'>
          <div className='abt_column'>
            {
              propertyDetails.amenities !== undefined &&
              propertyDetails.amenities.map(amenity =>
                <div key={uuidv4()} className='abt_line'>
                  <div style={{ marginRight: '5px', display: 'inline-block' }}>{amenity.label}</div>
                  <div style={{ marginLeft: '5px', marginRight: '5px', display: 'inline-block' }}>|</div>
                  <div style={{ marginLeft: '5px', display: 'inline-block' }}>{amenity.firstValue}</div>
                </div>
              )
            }
          </div>
        </div>
      </div>
    );
  }
}
