import React from 'react';
import uuidv4 from 'uuid/v4';
import moment from 'moment';

export default class LocationsOpenHouseTimes extends React.Component {
  render () {
    const { propertyDetails } = this.props;

    return (
      <div className='locations'>
        <div className='location1 desktop'>
          <div className='open_times'>Open House Times :</div>
          {ConvertOpenHouseTimes(propertyDetails.openHouseTimes)}
        </div>
        <div className='location2'>
          <div id='maps' />
          <div className='location1 mobile'>
            <div className='open_times'>Open House Times :</div>
            {ConvertOpenHouseTimes(propertyDetails.openHouseTimes)}
          </div>
          <div className='date_infos desktop'>
            <div className='loc_header'>Location :</div>
            <div className='loc_info'>{propertyDetails.address1}<br />{propertyDetails.address2}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const ConvertOpenHouseTimes = (openHouseTimes) => {
  if (openHouseTimes !== undefined) {
    return openHouseTimes.map(item =>
      <div key={uuidv4()} className='date_block animated_fastest'>
        {moment(item.dateRaw).format('dddd MMM Do')}
        <br />
        {moment(item.startTimeRaw).format('h:mma')} - {moment(item.endTimeRaw).format('h:mma')}
      </div>
    );
  }
};
