export const loadScript = (src, id, callback) => {
  const existingScript = document.getElementById(id);

  if (!existingScript) {
    const script = document.createElement('script');
    script.src = src;
    script.id = id;
    document.body.appendChild(script);

    script.onload = () => {
      if (callback) callback();
    };
  }

  if (existingScript && callback) callback();
};

var mapListsClosed = false;

export const initPropertyIdxUIActions = ($) => {
  $('.closer_arrow').click(function () {
    if (mapListsClosed === false) {
      $('.right_side').addClass('right_side_over');
      $('.searcher').addClass('right_side_over');
      $('.closer_arrow').addClass('arrow_over');
      $('.c_arrow img').addClass('rotated');
      mapListsClosed = true;
      return false;
    }
    if (mapListsClosed === true) {
      $('.right_side').removeClass('right_side_over');
      $('.searcher').removeClass('right_side_over');
      $('.closer_arrow').removeClass('arrow_over');
      $('.c_arrow img').removeClass('rotated');
      mapListsClosed = false;
      return false;
    }
  });

  $('.f_btn').click(function () {
    $('.f_btn').removeClass('f_actives');
    $(this).addClass('f_actives');
  });

  $('#list1').click(function () {
    $('#fullmap').addClass('desktop');
    $('.right_side').removeClass('desktop');
  });
  $('#list2').click(function () {
    $('#fullmap').removeClass('desktop');
    $('.right_side').addClass('desktop');
  });

  $('#h_1').click(function () {
    $('.right_side_overs').removeClass('hiddens2');
    $('.right_side').addClass('hiddens');
    $('#fullmap').hide();
    $('.closer_arrow, .searcher, #h_1').addClass('hiddens');
    $('.footer').removeClass('mobile');
  });

  $('#h_2').click(function () {
    $('.right_side_overs').addClass('hiddens2');
    $('.right_side').removeClass('hiddens');
    $('#fullmap').show();
    $('.closer_arrow, .searcher, #h_1').removeClass('hiddens');
    $('.footer').addClass('mobile');
  });
};

