import React from 'react';
import axios from 'axios';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import { toastr } from 'react-redux-toastr';
import NumberFormat from 'react-number-format';
import PhoneInput, { isPossiblePhoneNumber } from 'react-phone-number-input';
import 'react-phone-number-input/style.css';
import './PhoneInput.css';
import * as Consts from '../shared/Consts';

export default class OfferForm extends React.Component {
  constructor () {
    super();

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleValueChange = this.handleValueChange.bind(this);
    this.handleOfferPriceChange = this.handleOfferPriceChange.bind(this);
    this.handlePhoneNumberChange = this.handlePhoneNumberChange.bind(this);

    this.state = {
      FirstName: '',
      LastName: '',
      Email: '',
      Phone: '',
      Price: undefined,
      BuyingPlan: 'Loan',
      Toured: 'Yes',
      Comment: '',
      Submitting: false,
      Validation_FirstName: undefined,
      Validation_LastName: undefined,
      Validation_Email: undefined,
      Validation_Phone: undefined,
      Validation_Price: undefined
    };
  }

  handleValueChange (event) {
    this.setState({ [event.target.name]: event.target.value });
  }

  handleOfferPriceChange (event, name, value) {
    if (value.trim() !== '') {
      this.setState({ [name]: parseInt(value) });
    } else {
      this.setState({ [name]: undefined });
    }
  }

  handlePhoneNumberChange (event, name, value) {
    this.setState({ [name]: value });
  }

  async handleSubmit () {
    var valid = true;
    if (this.state.FirstName.trim() === '') {
      this.setState({ Validation_FirstName: Consts.VALIDATION_ERROR_REQUIRED_FIELD });
      valid = false;
    } else {
      this.setState({ Validation_FirstName: undefined });
    }

    if (this.state.LastName.trim() === '') {
      this.setState({ Validation_LastName: Consts.VALIDATION_ERROR_REQUIRED_FIELD });
      valid = false;
    } else {
      this.setState({ Validation_LastName: undefined });
    }

    if (this.state.Email.trim() === '') {
      this.setState({ Validation_Email: Consts.VALIDATION_ERROR_REQUIRED_FIELD });
      valid = false;
    } else {
      if (Consts.EMAIL_REX.test(this.state.Email.trim()) === false) {
        this.setState({ Validation_Email: Consts.VALIDATION_ERROR_INVALID_EMAIL });
        valid = false;
      } else {
        this.setState({ Validation_Email: undefined });
      }
    }

    if (this.state.Phone === undefined || this.state.Phone.trim() === '') {
      this.setState({ Validation_Phone: Consts.VALIDATION_ERROR_REQUIRED_FIELD });
      valid = false;
    } else if (isPossiblePhoneNumber(this.state.Phone) === false) {
      this.setState({ Validation_Phone: Consts.VALIDATION_ERROR_INVALID_PHONE });
      valid = false;
    } else {
      this.setState({ Validation_Phone: undefined });
    }

    if (this.state.Price !== undefined && this.state.Price !== '') {
      if (this.state.Price > 1000000000) {
        this.setState({ Validation_Price: Consts.VALIDATION_ERROR_NUMBER_TOO_LARGE });
        valid = false;
      } else {
        this.setState({ Validation_Price: undefined });
      }
    } else {
      this.setState({ Validation_Price: undefined });
    }

    if (valid === false) {
      return;
    }

    try {
      this.setState({ Submitting: true });
      const propertyDetails = this.props.propertyDetails;
      const url = `${process.env.REACT_APP_RIVERDALE_PUBLIC_API}/properties/${propertyDetails.propertyId}/offer`;
      const requestConfig = {
        headers: {
          'Content-Type': 'application/json; charset=utf-8',
          'X-Api-Key': process.env.REACT_APP_RIVERDALE_PUBLIC_API_KEY
        }
      };

      const offerPrice = (this.state.Price === '' || this.state.Price === undefined) ? undefined : this.state.Price;
      const body = {
        FirstName: this.state.FirstName,
        LastName: this.state.LastName,
        Email: this.state.Email,
        Phone: this.state.Phone,
        BuyingPlan: this.state.BuyingPlan,
        Toured: this.state.Toured,
        Comments: this.state.Comment,
        OfferPrice: offerPrice
      };

      await axios.post(url, body, requestConfig);
      this.setState({
        Submitting: false,
        FirstName: '',
        LastName: '',
        Email: '',
        Phone: '',
        Price: '',
        BuyingPlan: 'Loan',
        Toured: 'Yes',
        Comment: ''
      });

      toastr.success('Start an offer', 'Your offer has been successfully started.');
    } catch (error) {
      this.setState({ Submitting: false });
      console.log(error);
      toastr.error('Start an offer', 'Error! Please contact administrator!');
    }
  }

  render () {
    const { propertyDetails } = this.props;

    const inputStyle = { marginBottom: '5px' };
    return (
      <BlockUi
        tag='div'
        blocking={this.state.Submitting}
        loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
      >
        <div className='offer_form'>
          <div className='offer_header' style={{ marginBottom: '50px' }}>Start An Offer</div>
          <div className='form_offer'>
            <div className='inputer_form2 halfs3'>
              <input style={inputStyle} className='form1' placeholder='First Name' name='FirstName' value={this.state.FirstName} onChange={this.handleValueChange} />
              <div>First Name</div>
              {
                this.state.Validation_FirstName !== undefined &&
                  <div style={{ color: 'red' }}>{this.state.Validation_FirstName}</div>
              }

            </div>
            <div className='inputer_form2 halfs3'>
              <input style={inputStyle} className='form1' placeholder='Last Name' name='LastName' value={this.state.LastName} onChange={this.handleValueChange} />
              <div>Last Name</div>
              {
                this.state.Validation_LastName !== undefined &&
                  <div style={{ color: 'red' }}>{this.state.Validation_LastName}</div>
              }
            </div>
            <div className='inputer_form2 halfs3'>
              <input style={inputStyle} className='form1' placeholder='Email Address' name='Email' value={this.state.Email} onChange={this.handleValueChange} />
              <div>Email</div>
              {
                this.state.Validation_Email !== undefined &&
                  <div style={{ color: 'red' }}>{this.state.Validation_Email}</div>
              }
            </div>
            <div className='inputer_form2 halfs3'>
              <PhoneInput
                style={inputStyle}
                className='form1'
                defaultCountry='US'
                placeholder='Phone Number'
                value={this.state.Phone}
                onChange={(value) => { this.handlePhoneNumberChange(this, 'Phone', value); }}
              />
              <div>Phone Number</div>
              {
                this.state.Validation_Phone !== undefined &&
                  <div style={{ color: 'red' }}>{this.state.Validation_Phone}</div>
              }
            </div>

            <div className='add_infos'>Offer Price (Optional)
            </div>
            <div className='inputer_form2 fulls'>
              <NumberFormat
                thousandSeparator
                isNumericString
                prefix='$'
                decimalScale={2}
                allowNegative={false}
                className='form1'
                onValueChange={(values) => { const { value } = values; this.handleOfferPriceChange(this, 'Price', value); }}
                placeholder={propertyDetails.listPriceFormatted}
                value={this.state.Price}
                maxLength='14'
              />
              {
                this.state.Validation_Price !== undefined &&
                  <div style={{ color: 'red' }}>{this.state.Validation_Price}</div>
              }
            </div>
            <div className='form_inputs' style={{ textAlign: 'left' }}>
              <div className='t_texts'>How do you plan on buying?</div>
              <input type='radio' id='BuyingPlan_Loan' name='BuyingPlan' value='Loan' checked={this.state.BuyingPlan === 'Loan'} onChange={this.handleValueChange} />
              <label htmlFor='BuyingPlan_Loan'>Loan</label>
              <input type='radio' id='BuyingPlan_Cash' name='BuyingPlan' value='All Cash' checked={this.state.BuyingPlan === 'All Cash'} onChange={this.handleValueChange} />
              <label htmlFor='BuyingPlan_Cash'>All Cash</label>
            </div>
            <div className='form_inputs' style={{ textAlign: 'left' }}>
              <div className='t_texts'>Have you toured this home in person?</div>
              <input type='radio' id='Toured_Yes' name='Toured' value='Yes' checked={this.state.Toured === 'Yes'} onChange={this.handleValueChange} />
              <label htmlFor='Toured_Yes'>Yes</label>

              <input type='radio' id='Toured_No' name='Toured' value='No' checked={this.state.Toured === 'No'} onChange={this.handleValueChange} />
              <label htmlFor='Toured_No'>No</label>
            </div>
            <div className='inputer2' style={{ textAlign: 'left', marginTop: '0px' }}>Comments
              <textarea
                className='textform'
                style={{ maxWidth: 'none', marginTop: '5px' }}
                placeholder={`Hello, please help me put together an offer for ${propertyDetails.address1}, ${propertyDetails.address2}`}
                name='Comment'
                value={this.state.Comment}
                onChange={this.handleValueChange}
              />
            </div>
            <div className='bottom_btns'>
              <a href='/contact' className='save_btn3 animated_fastest'>Contact Us</a>
              <div className='next_btn3 animated_fastest' onClick={this.handleSubmit}>Continue</div>
              <div className='note desktop' />
              <div className='note'>
                <div className='inner_note'>Note: By continuing, you agree to our <a href='/terms-of-use/' className='email_btn_new'>Terms of Use</a> and <a href='/privacy-policy/' className='email_btn_new'>Privacy Policy</a></div>
              </div>
            </div>
          </div>
        </div>
      </BlockUi>
    );
  }
}
